<template>
  <div v-my-title data-title="List">
    <!--top begin-->
    <FHeader></FHeader>
    <SearchBox></SearchBox>
    <!--top end-->
    <article>
      <!--lbox begin-->
      <div class="lbox">
        <FColumn :channel="channel"></FColumn>
        <FBogList></FBogList>
        <!--bloglist end-->
      </div>
      <div class="rbox">
        <FRanking></FRanking>
        <FRecommend></FRecommend>
        <div class="ad whitebg imgscale">
          <ul>
            <a href="/"><img src="@/assets/images/ad.jpg"></a>
          </ul>
        </div>
        <FCloud></FCloud>
        <FGuessLike></FGuessLike>
        <div class="ad whitebg imgscale">
          <ul>
            <a href="/"><img src="@/assets/images/ad02.jpg"></a>
          </ul>
        </div>
        <FStatistic></FStatistic>
        <FLinks></FLinks>
      </div>
    </article>
    <FFooter></FFooter>
  </div>
</template>

<script>
import FHeader from "@/views/front/FHeader";
import SearchBox from "@/views/front/SearchBox";
import FRanking from "@/views/front/FRanking";
import FRecommend from "@/views/front/FRecommend";
import FCloud from "@/views/front/second/FCloud";
import FGuessLike from "@/views/front/FGuessLike";
import FStatistic from "@/views/front/FStatistic";
import FLinks from "@/views/front/FLinks";
import FFooter from "@/views/front/FFooter";
import FColumn from "@/views/front/second/FColumn";
import FBogList from "@/views/front/FBogList";

import {get} from "@/api/front"


export default {
  name: "List",
  components: {
    FBogList,
    FColumn,
    SearchBox, FHeader,
    FRanking, FRecommend, FGuessLike, FStatistic, FLinks,
    FFooter,
    FCloud
  },
  data() {
    return {
      channel: {},

    }
  },
  mounted() {
    this.getChannel(this.$route.params.id)
  },
  methods: {
    getChannel(id) {
      get(id).then(data => {
        this.channel = data.data
      }).catch(error => {
        this.$message.error(error)
      })
    }
  },
  // 仅在当前页面触发跳转路由动作时发生
  // 如果没有这个方法，当页面被加载过一次之后，仅改变路由页面将不会刷新，也不会有再执行mounted方法修改channel，结果就是list页面的跳转没反应
  beforeRouteUpdate(to, from, next) {
    this.getChannel(to.params.id)
    console.log(this.channelId)
    next()
  },

}
</script>

