<template>
  <div>
    <header id="header">
      <div class="navbox">
        <h2 id="mnavh"><span class="navicon"></span></h2>
        <div class="logo"><a href="#">个人博客</a></div>
        <nav>
          <ul id="starlist">
            <li><a href="index.html">首页</a></li>
            <li v-for="(item,index) in channels" :key="index" :class="{menu:item.children}">
                <router-link v-if="item.single" :to="'/channel_detail/'+item.id">{{ item.name }}</router-link>
                <router-link v-else :to="'/list/'+item.id">{{ item.name }}</router-link>
              <ul class="sub" v-if="item.children">
                <li v-for="(subItem, subIndex) in item.children" :key="subIndex">
                  <router-link v-if="subItem.single" :to="'/channel_detail/'+subItem.id">{{ subItem.name }}</router-link>
                  <router-link v-else :to="'/list/'+subItem.id">{{ subItem.name }}</router-link>
                </li>
              </ul>
            </li>
          </ul>
        </nav>
        <div class="searchico"></div>
      </div>
    </header>
  </div>
</template>

<script>
import channels from '@/data/nav'
import {queryByPos} from "@/api/front";

export default {
  name: "FHeader",
  data() {
    return {
      channels : [],
    }
  },
  mounted() {
    queryByPos('a').then(data=>{
      this.channels = data.data
    }).catch(error=>{
      this.$message.error(error)
    })
  }

}
</script>

