import Vue from 'vue'
import Vuex from 'vuex'
import token from './token'
import {login} from './api/user'

Vue.use(Vuex)

const store = new Vuex.Store({
    state:{
        token: token.get(),
    },
    mutations:{
        setToken(state, paramToken) {
            state.token = paramToken
            token.set(paramToken)
        },
        logout() {
            // 注意，这里不能写this.token.clear() , 因为这不修改token.js中的token，只会修改本文件中的token
           token.clear()
        }
    },
    actions:{
        login({commit}, user) {
            return new Promise((resolve, reject) => {
                // axios 调用该方法之后直接返回一个Promise实例
                login(user.userName, user.password).then(data => {
                    // 请求成功之后的回调函数， 重新设置token
                    console.log(data)
                    // 调用mutation修改token值
                    commit('setToken', data.data.token)
                    token.setUser(data.data.user)
                    // 执行成功之后由外部.then() 调用
                    resolve()
                }).catch(error => {
                    reject(error)
                })
            })

        }
    }
})

export default store