<template>
  <div>
    <el-form ref="defForm" :rules="rules" :model="defForm" label-width="120px">
      <el-form-item label="标题" prop="title">
        <el-input v-model="defForm.title"></el-input>
      </el-form-item>
      <el-form-item label="链接" prop="url">
        <el-input v-model="defForm.url"></el-input>
      </el-form-item>
      <el-form-item label="标题图片" prop="path">
        <el-upload
            class="avatar-uploader"
            action="string"
            :show-file-list="false"
            :http-request="uploadImg"
            :before-upload="beforeAvatarUpload">
          <img v-if="defForm.path" :src="defForm.path" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="打开方式" prop="target">
        <el-radio v-model="defForm.target" label="_blank">新窗口</el-radio>
        <el-radio v-model="defForm.target" label="_self">当前窗口</el-radio>
      </el-form-item>
      <el-form-item size="large">
        <el-button type="primary" @click="$router.back()">取消</el-button>
        <el-button type="primary" :loading="loading" @click="submitForm">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {upload, update, detail} from "@/api/friendlink";

export default {
  name: "Edit",
  data() {
    return {
      defForm: {
        title: '',
        path: '',
        url: '',
        target: '_blank',
      },
      loading: false,
      rules: {
        title:[{required:true, message:'必填项不能为空'}],
        url:[{required:true, message:'必填项不能为空'}],
      },
    }
  },
  created() {
    let id = this.$route.query.id
    detail(id).then(data =>{
      this.defForm = data.data
    }).catch(error => {
      this.$message.error(error)
    })
  },
  methods:{
    submitForm() {
      this.$refs['defForm'].validate(valid=>{
        if(valid) {
          this.loading=true
          // 提交之后发出新增的请求
          update(this.defForm).then(data=>{
            this.loading=false // 取消加载动作
            this.$message.success(data.msg)
            this.$router.back()
          }).catch(error=>{
            this.loading=false
            this.$message.error(error.msg)
          })
        }
      })
    },
    uploadImg(param) { // 上传标题图片
      // 自定义文件上传动作
      const formData = new FormData()
      formData.append('file', param.file)
      upload(formData).then(data=>{
        this.defForm.path  = data.data  // 上传图片成功之后返回的data.data是可以直接通过网络请求访问的图片url地址
      }).catch(error=>{
        this.$message.error(error)
      })
    },
    beforeAvatarUpload(file) {
      //上传之前的验证
      const isIMAGE = (file.type === 'image/jpeg') ||  (file.type === 'image/jpg') || (file.type ==='image/png')
      const isLt2M = file.size / 1024 / 1024 < 10
      if (!isIMAGE) {
        this.$message.error('上传文件只能是图片格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传文件大小不能超过 10MB!')
      }
      return isIMAGE && isLt2M
    },
  }

}
</script>

<style scoped>
.avatar-uploader {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  width: 80px;
  position: relative;
  overflow: hidden;
}
.avatar-uploader :hover {
  border-color: #409EFF;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 80px;
  height: 80px;
  line-height: 80px;
  text-align: center;
}
.avatar {
  width: 80px;
  height: 80px;
  display: block;
}

</style>