import Vue from 'vue'
import VueRouter from "vue-router";
import Login from './views/admin/Login'
import Index from "./views/admin/Index";
import User from './views/admin/user/Index'
import Tag from './views/admin/tag/Index'
import Comment from './views/admin/comment/Index'
import Info from "@/views/admin/user/Info";
import FriendLink from './views/admin/friendlink/Index'
import FriendLinkAdd from './views/admin/friendlink/Add'
import FriendLinkEdit from './views/admin/friendlink/Edit'

import Channel from './views/admin/channel/Index'
import ChannelAdd from '@/views/admin/channel/Add'
import ChannelEdit from '@/views/admin/channel/Edit'

import Article from './views/admin/article/Index'
import ArticleAdd from './views/admin/article/Add'
import ArticleEdit from './views/admin/article/Edit'

import FIndex from './views/front/Index'
import List from "@/views/front/second/List";
import Detail from "@/views/front/second/Detail";
import ChannelDetail from "@/views/front/second/ChannelDetail";

import token from "@/token";

Vue.use(VueRouter)

const router = new VueRouter({
    routes: [
        {
            path: '/',
            name: 'fIndex',
            component: FIndex,
            meta: {requireLogin: false}
        }, {
            path: '/list/:id',
            name: 'list',
            component: List,
            meta: {requireLogin: false}
        }, {
            path: '/detail/:id',
            name: 'detail',
            component: Detail,
            meta: {requireLogin: false}
        }, {
            path: '/channel_detail/:id',
            name: 'channel_detail',
            component: ChannelDetail,
            meta: {requireLogin: false}
        },
        {
            path: '/login',
            name: 'login',
            component: Login
        },
        {
            path: '/index',
            name: 'index',
            component: Index,
            children: [
                {
                    path: '/info',
                    name: 'info',
                    component: Info,
                },
                {
                    path: '/user',
                    name: 'user',
                    component: User,
                },
                {
                    path: '/tag',
                    name: 'tag',
                    component: Tag,
                },
                {
                    path: '/article',
                    name: 'article',
                    component: Article,
                },
                {
                    path: '/friend-link',
                    name: 'friend-link',
                    component: FriendLink,
                },
                {
                    path: '/friend-link_add',
                    name: 'friend-link_add',
                    component: FriendLinkAdd,
                },
                {
                    path: '/friend-link_edit',
                    name: 'friend-link_edit',
                    component: FriendLinkEdit,
                },
                {
                    path: '/comment',
                    name: 'comment',
                    component: Comment,
                },
                {
                    path: '/channel',
                    name: 'channel',
                    component: Channel,
                },
                {
                    path: '/channel_edit',
                    name: 'channel_edit',
                    component: ChannelEdit,
                },
                {
                    path: '/channel_add',
                    name: 'channel_add',
                    component: ChannelAdd,
                },
                {
                    path: '/article',
                    name: 'article',
                    component: Article,
                },
                {
                    path: '/article_edit',
                    name: 'article_edit',
                    component: ArticleEdit,
                },
                {
                    path: '/article_add',
                    name: 'article_add',
                    component: ArticleAdd,
                },
            ]
        }
    ]
})

router.beforeEach((to, from, next) => {
    // some函数设计的不好，应该设计成向上兼容
    if (!to.matched.some(record => record.meta.requireLogin)) { // 全false才放行，其中有一个true则需要验证
        next()
    } else {
        if (token.get()) {
            next()
        } else {
            if (to.path === '/login') {
                next()
            } else {
                next('/login')
            }
        }
    }
})

export default router