<template>
    <el-row>
      <el-col :span="20">
        <table  class="logo" border="0">
          <tr>
            <td>
              <img src="../../assets/logo3.png"/>
            </td>
            <td>CMS系统</td>
          </tr>
        </table>
      </el-col>
      <el-col :span="4" align="right">
        <el-dropdown trigger="click" @command="handleCommand" style="margin-top: 20px;">
          <el-link class="user" :underline="false">
            <table>
              <tr>
                <td>
                  <img :src="avatar"/>
                </td>
                <td>{{ userName }}</td>
                <td>
                  <el-icon class="el-icon-caret-bottom" />
                </td>
              </tr>
            </table>
          </el-link>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="index">网站首页</el-dropdown-item>
            <el-dropdown-item command="info">个人信息</el-dropdown-item>
            <el-dropdown-item command="password">修改密码</el-dropdown-item>
            <el-dropdown-item command="logout">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-col>
    </el-row>
</template>

<script>
import token from "../../token";

export default {
  name: "Header",
  data() {
    return {
      userName : token.getUser().userName,
      avatar:token.getUser().avatar,
    }
  },
  methods: {
    handleCommand(command) {
      if(command == 'index') {
        this.$router.push('/index')
      }
      if(command == 'info') {
        this.$router.push('/info')
      }
      if(command == 'password') {
        this.$router.push('/password')
      }
      if(command == 'logout') {
        this.$store.commit('logout')
        this.$router.push('/login')
      }
    }
  }
}
</script>

<style scoped>

.logo{
  height: 60px;
  color: white;
  font-size: 20px;
  font-weight: bold;
}
.logo img{
  height: 40px;
  width: 40px;
}
.user{
  color: white !important;
  font-size: 15px;
}
.user img{
  width: 20px;
  height: 20px;
  border-radius: 10px;
}


</style>