<template>
  <div v-my-title data-title="详情">
    <!--top begin-->
    <FHeader></FHeader>
    <SearchBox></SearchBox>
    <!--top end-->
    <article>
      <!--lbox begin-->
      <div class="lbox">
        <FContent :content="article"></FContent>
        <FRelate></FRelate>
        <FComment></FComment>
        <!--bloglist end-->
      </div>
      <div class="rbox">
        <FRanking></FRanking>
        <FRecommend></FRecommend>
        <div class="ad whitebg imgscale">
          <ul>
            <a href="/"><img src="@/assets/images/ad.jpg"></a>
          </ul>
        </div>
        <FCloud></FCloud>
        <FGuessLike></FGuessLike>
        <div class="ad whitebg imgscale">
          <ul>
            <a href="/"><img src="@/assets/images/ad02.jpg"></a>
          </ul>
        </div>
        <FStatistic></FStatistic>
        <FLinks></FLinks>
      </div>
    </article>
    <FFooter></FFooter>
  </div>
</template>

<script>
import FHeader from "@/views/front/FHeader";
import SearchBox from "@/views/front/SearchBox";
import FRanking from "@/views/front/FRanking";
import FRecommend from "@/views/front/FRecommend";
import FCloud from "@/views/front/second/FCloud";
import FGuessLike from "@/views/front/FGuessLike";
import FStatistic from "@/views/front/FStatistic";
import FLinks from "@/views/front/FLinks";
import FFooter from "@/views/front/FFooter";
import FContent from "@/views/front/second/FContent";
import FRelate from "@/views/front/second/FRelate";
import FComment from "@/views/front/second/FComment";

import {getArticle} from "@/api/front";

export default {
  name: "Detail",
  components: {
    FComment,
    FRelate,
    FContent,
    SearchBox, FHeader,
    FRanking, FRecommend, FGuessLike, FStatistic, FLinks,
    FFooter,
    FCloud
  },
  data() {
    return{
      article:{},
    }
  },
  mounted() {
    getArticle(this.$route.params.id).then(data=>{
      this.article = data.data
    }).catch(error=>{
      this.$message.error(error)
    })
  }
  ,
  beforeRouteUpdate(to, from, next) {
    // 在当前路由改变，但是该组件被复用时调用
    // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
    // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
    // 可以访问组件实例 `this`
  },

}
</script>

