<template>
  <div>
    <div>
      <el-form ref="queryForm" :inline="true" :model="queryForm" label-width="80px">
        <el-form-item>
          <el-input v-model="queryForm.title" placeholder="按标题查询"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" icon="el-icon-search" @click="search">查询</el-button>
          <el-button type="success" icon="el-icon-plus" @click="append">新增</el-button>
        </el-form-item>
      </el-form>
    </div>
    <el-table
        :data="tableData"
        stripe
        style="width: 100%">
      <el-table-column
          prop="id"
          label="ID"
          width="180">
      </el-table-column>
      <el-table-column
          prop="title"
          label="标题"
          width="180">
      </el-table-column>
      <el-table-column
          prop="url"
          label="链接"
          width="180">
      </el-table-column>
      <el-table-column
          prop="target"
          label="打开方式"
          width="180">
      </el-table-column>
      <el-table-column
          prop="content"
          label="内容"
          width="180">
      </el-table-column>
      <el-table-column
          align="center"
          width="150px"
          label="操作">
        <template slot-scope="scope">
          <el-link @click="edit(scope.row)" :underline="false" type="primary" class="el-icon-edit-outline">编辑</el-link>
          <el-link @click="del(scope.row)" :underline="false" type="danger" class="el-icon-delete">删除</el-link>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
        background
        layout="total, prev, pager, next, jumper"
        @current-change="handleCurrentChange"
        :current-page="pageNo"
        :total="total">
    </el-pagination>

<!--    <el-dialog :visible.sync="addVisible" v-if="addVisible" :close-on-click-modal="false">-->
<!--      <Add @after="search" @hideDialog="hideDialog"></Add>-->
<!--    </el-dialog>-->

    <!--    当子组件退出时，例如edit和add窗口退出，会触发$emit方法，即hideDialog方法，修改editVisible为false，
              v-if此时会清除原有的dom元素（Edit数据）, v-if="editVisible"  --->
<!--    <el-dialog :visible.sync="editVisible" v-if="editVisible" :close-on-click-modal="false">-->
<!--      <Edit @after="search" :data="formData"  @hideDialog="hideDialog"></Edit>-->
<!--    </el-dialog>-->
  </div>
</template>

<script>
import {del, query} from "@/api/friendlink";

export default {
  name: "Index",
  components: '',
  data() {
    return {
      queryForm: {
       title: '',

      },
      tableData: [],
      total: 0,
      pageNo: 1,
      editVisible: '',
      addVisible: '',
    }
  },
  mounted() { // 挂载，显示时自动执行该方法？
    this.list({"page": this.pageNo})
  },
  methods: {
    list(param) {
      query(param).then(data => {
        this.tableData = data.list
        this.total = data.total
      }).catch(error => {
        this.$message.error(error)
      })
    },
    handleCurrentChange(val) { // 跳转到某一页
      let param = this.queryForm
      this.pageNo = val
      param.page = this.pageNo
      this.list(param)
    },
    search() {
      let param = this.queryForm
      this.pageNo = 1
      param.page = this.pageNo
      this.list(param)
    },
    del(row) {
      this.$confirm('确定要删除吗？', '提示').then(() => {
        del(row.id).then(data => {
          console.log(data)
          let param = this.queryForm
          param.page = this.pageNo
          this.list(param)
        }).catch(error => {
          this.$message.error(error)
        })
      }).catch(error => {
        this.$message.error(error)
      })
    },
    append() {
      this.$router.push('/friend-link_add')
    },
    hideDialog() {
      // 关闭窗口
      this.editVisible = false
    },
    edit(row) {
      this.$router.push({
        path: '/friend-link_edit',
        query: {
          id: row.id
        },
      })
    }
  }
}
</script>

<style scoped>

</style>