import axiosInstance from './axiosInstance';

export function queryByPos(pos) {
    return axiosInstance.get('front/channel/queryByPos?pos=' + pos)
}
export function get(id) {
    return axiosInstance.get('front/channel/get?id=' + id)
}
/**
 * 获取所有栏目下的最新文章列表
 * @returns {Promise<AxiosResponse<any>>}
 */
export function getNew() {
    return axiosInstance.get('front/article/getList')
}

/**
 * 获取前几条数据
 * @param channelId
 * @param top 前几条参数
 * @returns {Promise<AxiosResponse<any>>}
 */
export function getTop(channelId,top) {
    return axiosInstance.get('front/article/getList?channelId='+channelId+"&limit="+top);
}
/**
 * 获取指定文章ById
 * @param id
 * @returns {Promise<AxiosResponse<any>>}
 */
export function getArticle(id) {
    return axiosInstance.get('front/article/get?id='+id);
}





