<template>
  <div>
    <el-form ref="defForm" :rules="rules" :model="defForm" label-width="80px">
      <el-form-item label="账号" prop="userName">
        <el-input v-model="defForm.userName"></el-input>
      </el-form-item>
      <el-form-item label="昵称" prop="nickName">
        <el-input v-model="defForm.nickName"></el-input>
      </el-form-item>
      <el-form-item label="邮箱" prop="email">
        <el-input v-model="defForm.email"></el-input>
      </el-form-item>
      <el-form-item label="状态" prop="status">
        <el-select v-model="defForm.status" placeholder="请选择状态">
          <el-option
              v-for="item in this.utils.status"
              :key="item.value"
              :value="item.value"
              :label="item.label">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item size="large">
        <el-button type="primary" @click="close">关闭</el-button>
        <el-button type="primary" :loading="loading" @click="submitForm">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {update} from '@/api/user'
export default {
  name: "Update",
  props: ['data'],
  created() {  // 在页面渲染时，将数据传给defForm
    // this.defForm = this.data  // 将props接收的数据传给表单, 问题：this.defForm和this.data会指向同一个内存地址，会同步修改
    // 在编辑操作退出，并不提交时，页面没有提交重新查询的请求，导致暂时出现页面数据是修改未保存的数据
    this.defForm = JSON.parse(JSON.stringify(this.data))
    console.log('--------')
    console.log(this.defForm)
    // 这里没有触发
  },
  data() {
    return {
      defForm: {
        id:'',
        userName: '',
        password: '',
        nickName: '',
        email: '',
        status: 'T',
      },
      loading: false,
      rules: {
        userName:[{required:true, message:'用户名不能为空'}],
        password:[{required:true, message:'密码不能为空'}]
      }
    }
  },
  methods:{
    close() {
      this.$emit('hideDialog')
    },
    submitForm() {
      this.$refs['defForm'].validate(valid=>{
        if(valid) {
          this.loading=true
          // 提交之后发出新增的请求
          update(this.defForm).then(data=>{
            console.log(data)
            this.loading=false // 取消加载动作
            this.$refs['defForm'].resetFields()  // 重置表单
            // 子组件向父组件传递方法，使得新增之后，用户立即自动出现新增的用户信息
            this.$emit('after')
            this.$emit('hideDialog')
          }).catch(error=>{
            this.loading=false
            console.log(error)
          })
        }
      })
    },
  }

}
</script>

