<template>
  <div>
    <el-form ref="defForm" :rules="rules" :model="defForm" label-width="80px">
      <el-form-item label="标签名" prop="tagName">
        <el-input v-model="defForm.tagName"></el-input>
      </el-form-item>
      <el-form-item size="large">
        <el-button type="primary" @click="close">关闭</el-button>
        <el-button type="primary" :loading="loading" @click="submitForm">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {update} from '@/api/tag'
export default {
  name: "Update",
  props: ['data'],
  created() {  // 在页面渲染时，将数据传给defForm
    // this.defForm = this.data  // 将props接收的数据传给表单, 问题：this.defForm和this.data会指向同一个内存地址，会同步修改
    // 在编辑操作退出，并不提交时，页面没有提交重新查询的请求，导致暂时出现页面数据是修改未保存的数据
      this.defForm = JSON.parse(JSON.stringify(this.data))
  },
  data() {
    return {
      defForm: {
        id:'',
        tagName: '',
      },
      loading: false,
      rules: {
        tagName:[{required:true, message:'必填项不能为空'}],
      }
    }
  },
  methods:{
    close() {
      this.$emit('hideDialog')
    },
    submitForm() {
      this.$refs['defForm'].validate(valid=>{
        if(valid) {
          this.loading=true
          // 提交之后发出新增的请求
          update(this.defForm).then(data=>{
            console.log(data)
            this.loading=false // 取消加载动作
            this.$refs['defForm'].resetFields()  // 重置表单
            // 子组件向父组件传递方法，使得新增之后，用户立即自动出现新增的用户信息
            this.$emit('after')
            this.$emit('hideDialog')
          }).catch(error=>{
            this.loading=false
            console.log(error)
          })
        }
      })
    },
  }

}
</script>

