<template>
  <div>
    <el-form ref="defForm" :rules="rules" :model="defForm" label-width="120px">
      <el-form-item label="栏目名称" prop="name">
        <el-input v-model="defForm.name"></el-input>
      </el-form-item>
      <el-form-item label="父栏目" prop="parentId">
        <treeselect v-model="defForm.parentId" :multiple="false" :options="treeData"/>
      </el-form-item>
      <el-form-item label="栏目图片" prop="channelImg">
        <el-upload
            class="avatar-uploader"
            action="string"
            :show-file-list="false"
            :http-request="uploadImg"
            :before-upload="beforeAvatarUpload">
          <img v-if="defForm.channelImg" :src="defForm.channelImg" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
        <!--        <el-input v-model="defForm.channelImg"></el-input>-->
      </el-form-item>
      <el-form-item label="摘要" prop="digest">
        <el-input v-model="defForm.digest"></el-input>
      </el-form-item>
      <el-form-item label="位置" prop="pos">
        <el-select v-model="defForm.pos" placeholder="请选择位置">
          <el-option
              v-for="item in this.posList"
              :key="item"
              :value="item"
              :label="item">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="是否单页" prop="single">
        <el-switch
            v-model="defForm.single"
            active-color="#13ce66"
            inactive-color="#999999">
        </el-switch>
      </el-form-item>
      <el-form-item label="访问链接" prop="url">
        <el-input v-model="defForm.url"></el-input>
      </el-form-item>
      <el-form-item label="seoTitle" prop="seoTitle">
        <el-input v-model="defForm.seoTitle"></el-input>
      </el-form-item>
      <el-form-item label="SeoKeyword" prop="seoKeyword">
        <el-input v-model="defForm.seoKeyword"></el-input>
      </el-form-item>
      <el-form-item label="seoDescription" prop="seoDescription">
        <el-input v-model="defForm.seoDescription"></el-input>
      </el-form-item>
      <el-form-item label="内容" prop="content">
        <v-md-editor v-model="defForm.content" height="400px"></v-md-editor>
      </el-form-item>
      <el-form-item size="large">
        <el-button type="primary" @click="$router.back()">取消</el-button>
        <el-button type="primary" :loading="loading" @click="submitForm">新增</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {create, tree} from '@/api/channel'
// import the component
import Treeselect from '@riophae/vue-treeselect'
// import the styles
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
import {upload} from "@/api/channel";

export default {
  name: "Add",
  // register the component
  components: {Treeselect},
  data() {
    return {
      defForm: {
        name: '',
        parentId: null,
        channelImg: '',
        digest: '',
        single: false,
        url: '',
        seoTitle: '',
        seoKeyword: '',
        seoDescription: '',
        content: '',
        pos: '',
      },
      posList: ['A', 'B', 'C', 'D', 'E', 'F'],
      treeData: [],
      loading: false,
      rules: {}
    }
  },
  created() {
    tree().then(data => {
      this.treeData = data.data
    }).catch(error => {

    })
  },
  methods: {
    close() {
      this.$emit('hideDialog')
    },
    submitForm() {
      this.$refs['defForm'].validate(valid => {
        if (valid) {
          console.log(this.defForm)
          if (this.defForm.single) {
            this.defForm.single = 'Y'
          } else {
            this.defForm.single = 'N'
          }
          if (this.defForm.parentId == null) {
            this.defForm.parentId = 0
          }
          this.loading = true
          // 提交之后发出新增的请求
          create(this.defForm).then(data => {
            console.log(data)
            this.loading = false // 取消加载动作
            this.$refs['defForm'].resetFields()  // 重置表单
            this.$message.success(data.msg)
            this.$router.back()
          }).catch(error => {
            this.loading = false
            this.$message.error('新增失败')
          })
        }
      })
    },
    uploadImg(param) {
      // 自定义文件上传动作
      const formData = new FormData()
      formData.append('file', param.file)
      upload(formData).then(data => {
        this.defForm.channelImg = data.data
      }).catch(error => {
        this.$message.error(error)
      })

    },
    beforeAvatarUpload(file) {
      //上传之前的验证
      const isIMAGE = (file.type === 'image/jpeg') || (file.type === 'image/jpg') || (file.type === 'image/png')
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isIMAGE) {
        this.$message.error('上传文件只能是图片格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传文件大小不能超过 2MB!')
      }
      return isIMAGE && isLt2M
    }
  }

}
</script>

<style scoped>
.avatar-uploader {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  width: 80px;
  position: relative;
  overflow: hidden;
}

.avatar-uploader :hover {
  border-color: #409EFF;
}

.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 80px;
  height: 80px;
  line-height: 80px;
  text-align: center;
}

.avatar {
  width: 80px;
  height: 80px;
  display: block;
}

</style>