<template>
  <div>
    <el-form ref="defForm" :rules="rules" :model="defForm" label-width="120px">
      <el-form-item label="标题" prop="title">
        <el-input v-model="defForm.title"></el-input>
      </el-form-item>
      <el-form-item label="栏目" prop="channelId">
        <treeselect v-model="defForm.channelId" :multiple="false" :options="treeData" />
      </el-form-item>
      <el-form-item label="标题图片" prop="titleImg">
        <el-upload
            class="avatar-uploader"
            action="string"
            :show-file-list="false"
            :http-request="uploadImg"
            :before-upload="beforeAvatarUpload">
          <img v-if="defForm.titleImg" :src="defForm.titleImg" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="摘要" prop="summary">
        <el-input v-model="defForm.summary"></el-input>
      </el-form-item>
      <el-form-item label="作者" prop="author">
        <el-input v-model="defForm.author"></el-input>
      </el-form-item>
      <el-form-item label="外链" prop="url">
        <el-input v-model="defForm.url"></el-input>
      </el-form-item>
      <el-form-item label="允许评论" prop="commentStatus">
        <el-switch
            v-model="defForm.commentStatus"
            active-color="#13ce66"
            inactive-color="#999999">
        </el-switch>
      </el-form-item>
      <el-form-item label="是否轮播" prop="rotation">
        <el-switch
            v-model="defForm.rotation"
            active-color="#13ce66"
            inactive-color="#999999">
        </el-switch>
      </el-form-item>
      <el-form-item label="置顶" prop="top">
        <el-switch
            v-model="defForm.top"
            active-color="#13ce66"
            inactive-color="#999999">
        </el-switch>
      </el-form-item>
      <el-form-item label="标签" prop="selectedTagList">
        <el-checkbox-group v-model="defForm.selectedTagList">
          <el-checkbox v-for="(item, index) in tagList" :key="index" :label='item.id' >{{item.tagName}}</el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="附件上传" prop="attachmentList">
        <el-upload
            class="upload-demo"
            action="string"
            :before-remove="beforeRemove"
            multiple
            :limit="3"
            :http-request="uploadImg2"
            :on-exceed="handleExceed"
            :file-list="defForm.attachmentList">
          <el-button size="small" type="primary">点击上传</el-button>
          <div slot="tip" class="el-upload__tip">上传类型为jpg/png/gif文件，且不超过5000kb</div>
        </el-upload>
      </el-form-item>
      <el-form-item label="内容" prop="content">
        <v-md-editor v-model="defForm.content" height="400px"></v-md-editor>
      </el-form-item>
      <el-form-item size="large">
        <el-button type="primary" @click="$router.back()">取消</el-button>
        <el-button type="primary" :loading="loading" @click="submitForm">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import VueMarkdown from 'vue-markdown'
import {tree} from '@/api/channel'
// import the component
import Treeselect from '@riophae/vue-treeselect'
// import the styles
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
import {upload, update, detail} from "@/api/article";
import {all} from '@/api/tag'

export default {
  name: "Edit",
  // register the component
  components: { Treeselect,  },
  data() {
    return {
      tagList: [],
      defForm: {
        name: '',
        channelId: null,
        titleImg: '',
        summary: '',
        author: '',
        url: '',
        commentStatus: true ,
        rotation:false,
        top: false,
        content:'',
        attachmentList: [],
        selectedTagList: [],
      },
      treeData: [],
      loading: false,
      rules: {
        title:[{required:true, message:'必填项不能为空'}],
        channelId:[{required:true, message:'必填项不能为空'}]
      },
    }
  },
  created() {
    let id = this.$route.query.id
    detail(id).then(data=>{
      this.defForm = data.data
      if (data.data.commentStatus === 1) {
        this.defForm.commentStatus = false
      } else {
        this.defForm.commentStatus = true
      }
      if (data.data.rotation === 1) {
        this.defForm.rotation = true
      } else {
        this.defForm.rotation = false
      }
      if (data.data.top === 1) {
        this.defForm.top = true
      } else {
        this.defForm.top = false
      }
      if (!data.data.selectedTagList) {
        this.defForm.selectedTagList = []
      }
      if (!data.data.attachmentList) {
        this.defForm.attachmentList = []
      }

    }).catch(error=>{
      this.$message.error(error.msg)
    })

    tree().then(data=>{
      this.treeData = data.data
    }).catch(error=>{
      this.$message.error(error.msg)
    })
    all().then(data => {
      this.tagList = data.data
    }).catch(error => {
      this.$message.error(error.msg)
    })
  },
  methods:{
    submitForm() {
      this.$refs['defForm'].validate(valid=>{
        if(valid) {
          this.loading=true
          if (this.defForm.commentStatus) {
            this.defForm.commentStatus = 0
          } else {
            this.defForm.commentStatus = 1
          }
          if (this.defForm.rotation) {
            this.defForm.rotation = 1
          } else {
            this.defForm.rotation = 0
          }
          if (this.defForm.top) {
            this.defForm.top = 1
          } else {
            this.defForm.top = 0
          }
          // 提交之后发出新增的请求
          update(this.defForm).then(data=>{
            this.loading=false // 取消加载动作
            // this.$refs['defForm'].resetFields()  // 重置表单
            this.$message.success(data.msg)
            this.$router.back()
          }).catch(error=>{
            this.loading=false
            this.$message.error('修改失败')
          })
        }
      })
    },
    uploadImg(param) { // 上传标题图片
      // 自定义文件上传动作
      const formData = new FormData()
      formData.append('file', param.file)
      upload(formData).then(data=>{
        this.defForm.titleImg = data.data  // 上传图片成功之后返回的data.data是可以直接通过网络请求访问的图片url地址
      }).catch(error=>{
        this.$message.error(error)
      })
    },
    uploadImg2(param) { // 上传图片附件
      // 自定义文件上传动作2
      const formData = new FormData()
      formData.append('file', param.file)  // 加入了二进制文件
      upload(formData).then(data=>{
        // 将上传文件的URL地址放入attachmentList中
        this.defForm.attachmentList.push({name: param.file.name, url:data.data})
        console.log(this.defForm.attachmentList)
      }).catch(error=>{
        this.$message.error(error)
      })
    },
    beforeAvatarUpload(file) {
      //上传之前的验证
      const isIMAGE = (file.type === 'image/jpeg') ||  (file.type === 'image/jpg') || (file.type ==='image/png')
      const isLt2M = file.size / 1024 / 1024 < 10
      if (!isIMAGE) {
        this.$message.error('上传文件只能是图片格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传文件大小不能超过 10MB!')
      }
      return isIMAGE && isLt2M
    },
    handleExceed(files, attachmentList) {
      this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + attachmentList.length} 个文件`);
    },
    beforeRemove(file, attachmentList) {
      this.$confirm(`确定移除${file.name}`)
      attachmentList.forEach((item, index)=>{
        if (file.name == item.name) {
          this.defForm.attachmentList.splice(index, 1)  // 在index位置开始删去一个文件
        }
      })
      // return this.$confirm(`确定移除 ${ file.name }？`);
    }
  }

}
</script>

<style scoped>
.avatar-uploader {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  width: 80px;
  position: relative;
  overflow: hidden;
}
.avatar-uploader :hover {
  border-color: #409EFF;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 80px;
  height: 80px;
  line-height: 80px;
  text-align: center;
}
.avatar {
  width: 80px;
  height: 80px;
  display: block;
}

</style>