<template>
  <div v-my-title data-title="Blog">
    <!--top begin-->
    <FHeader></FHeader>
    <SearchBox></SearchBox>
    <!--top end-->
    <article>
      <!--lbox begin-->
      <div class="lbox">
        <!--banbox begin-->
        <FSwiper></FSwiper>
        <!--banbox end-->
        <!--headline begin-->
        <FHeadLine></FHeadLine>
        <!--headline end-->
        <div class="clearblank"></div>
        <FTabBox></FTabBox>
        <!--tab_box end-->
        <FSubject></FSubject>
        <div class="ad whitebg"><img src="@/assets/images/longad.jpg"></div>
        <FBogList :articles="articles"></FBogList>
        <!--bloglist end-->
      </div>
      <div class="rbox">
        <FMe></FMe>
        <FNotice></FNotice>
        <FRanking></FRanking>
        <FRecommend></FRecommend>
        <div class="ad whitebg imgscale">
          <ul>
            <a href="/"><img src="@/assets/images/ad.jpg"></a>
          </ul>
        </div>
        <FGuessLike></FGuessLike>
        <div class="ad whitebg imgscale">
          <ul>
            <a href="/"><img src="@/assets/images/ad02.jpg"></a>
          </ul>
        </div>
        <FStatistic></FStatistic>
        <FLinks></FLinks>
      </div>
    </article>
    <FFooter></FFooter>
  </div>
</template>

<script>
import '@/assets/css/base.css'
import '@/assets/css/m.css'

import FHeader from "@/views/front/FHeader";
import SearchBox from "@/views/front/SearchBox";
import FSwiper from "@/views/front/FSwiper";
import FHeadLine from "@/views/front/FHeadLine";

import FTabBox from "@/views/front/FTabBox";
import FSubject from "@/views/front/FSubject";
import FBogList from "@/views/front/FBogList";

import FNotice from "@/views/front/FNotice";
import FMe from "@/views/front/FMe";
import FRanking from "@/views/front/FRanking";
import FRecommend from "@/views/front/FRecommend";
import FGuessLike from "@/views/front/FGuessLike";
import FStatistic from "@/views/front/FStatistic";
import FLinks from "@/views/front/FLinks";

import FFooter from "@/views/front/FFooter";

import {getNew} from "@/api/front";

export default {
  name: "Index",
  components: {
    SearchBox, FHeader,
    FSwiper, FHeadLine, FTabBox, FSubject, FBogList,
    FNotice, FMe, FRanking, FRecommend, FGuessLike, FStatistic, FLinks,
    FFooter
  },
  data() {
    return {
      articles: [],
    }
  },
  mounted() {
    getNew().then(data=>{
      this.articles = data.list
    }).catch(error=>{
      this.$message.error(error)
    })
  }
}
</script>

