<template>
  <div class="banbox">
    <swiper class="swiper" :options="swiperOption" style="height: 260px">
      <swiper-slide v-for="(item,index) in sliders" :key="index">
        <a :href="item.url" target="_blank">
          <img :src="item.img"/>
        </a>
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
      <div class="swiper-button-prev" slot="button-prev"></div>
      <div class="swiper-button-next" slot="button-next"></div>
    </swiper>
  </div>
</template>

<script>
import {Swiper, SwiperSlide} from 'vue-awesome-swiper'
// import { Swiper, SwiperSlide } from 'swiper/vue';

import SwiperCore, {Navigation, Pagination, Scrollbar, A11y, Autoplay} from 'swiper';
// Import Swiper styles
// import 'swiper/swiper.scss';
// import 'swiper/components/navigation/navigation.scss';
// import 'swiper/components/pagination/pagination.scss';
// import 'swiper/components/scrollbar/scrollbar.scss';
import 'swiper/swiper-bundle.css'
// install Swiper components
SwiperCore.use([Autoplay, Navigation, Pagination, Scrollbar, A11y]);

export default {
  name: "FSwiper",
  components: {
    Swiper,
    SwiperSlide
  },
  methods: {},
  data() {
    return {
      swiperOption: {
        slidesPerView: 1,
        spaceBetween: 30,
        loop: true,
        autoplay: true,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        },
      },
      sliders: [
        {
          "img": "https://img3.doubanio.com/img/files/file-1485236684-0.jpg",
          "url": "https://www.baidu.com"
        },
        {
          "img": "https://img3.doubanio.com/img/files/file-1487144115-0.jpg",
          "url": "https://www.baidu.com"
        },
        {
          "img": "https://img3.doubanio.com/img/files/file-1486953805-0.jpg",
          "url": "https://www.baidu.com"
        }
      ],
    }
  }
}
</script>

<style scoped>
.swiper-container {
  --swiper-theme-color: #ff6600;
  --swiper-pagination-color: #00ccff; /* 两种都可以 */
}
</style>