<template>
  <div>
    <div>
      <el-form ref="queryForm" inline="true" :model="queryForm" label-width="80px">
        <el-form-item>
          <el-input v-model="queryForm.userName" placeholder="按姓名查询"></el-input>
        </el-form-item>
        <el-form-item>
          <el-input v-model="queryForm.nickName" placeholder="按昵称查询"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" icon="el-icon-search" @click="search">查询</el-button>
          <el-button type="success" icon="el-icon-plus" @click="append">新增</el-button>
        </el-form-item>
      </el-form>
    </div>
    <el-table
        :data="tableData"
        stripe
        style="width: 100%">
      <el-table-column
          prop="id"
          label="ID"
          width="180">
      </el-table-column>
      <el-table-column
          prop="userName"
          label="用户名"
          width="180">
      </el-table-column>
      <el-table-column
          prop="nickName"
          label="昵称">
      </el-table-column>
      <el-table-column
          prop="email"
          label="邮箱">
      </el-table-column>
      <el-table-column
          prop="createDate"
          label="创建时间">
      </el-table-column>
      <el-table-column
          prop="status"
          label="状态">
      </el-table-column>
      <el-table-column
          align="center"
          width="150px"
          label="操作">
          <template slot-scope="scope">
            <el-link @click="edit(scope.row)" :underline="false" type="primary" class="el-icon-edit-outline">编辑</el-link>
            <el-link @click="del(scope.row)" :underline="false" type="danger" class="el-icon-delete">删除</el-link>
          </template>
      </el-table-column>
    </el-table>
    <el-pagination
        background
        layout="total, prev, pager, next, jumper"
        @current-change="handleCurrentChange"
        :current-page="pageNo"
        :total="total">
    </el-pagination>

    <el-dialog :visible.sync="addVisible" v-if="addVisible" :close-on-click-modal="false">
      <Add @after="search" @hideDialog="hideDialog"></Add>
    </el-dialog>

<!--    当子组件退出时，例如edit和add窗口退出，会触发$emit方法，即hideDialog方法，修改editVisible为false，
          v-if此时会清除原有的dom元素（Edit数据）, v-if="editVisible"  --->
    <el-dialog :visible.sync="editVisible" v-if="editVisible" :close-on-click-modal="false">
      <Edit @after="search" :data="formData"  @hideDialog="hideDialog"></Edit>
    </el-dialog>

  </div>
</template>

<script>
import {del, query} from "@/api/user";
import Edit from "@/views/admin/user/Edit";
import Add from "@/views/admin/user/Add";

export default {
  name: "Index",
  components: {Add, Edit},
  data() {
    return {
      queryForm:{
        userName:'',
        nickName:'',
      },
      tableData: [],
      total:0,
      pageNo:1,
      addVisible:false,
      editVisible:false,
      formData:{},
    }
  },
  mounted() { // 挂载，显示时自动执行该方法？
    this.list({"page": this.pageNo})
  },
  methods:{
    list(param) {
      query(param).then(data=>{
        this.tableData = data.list
        this.total = data.total
        // console.log(this.pageNo)
      }).catch(error=>{
        this.$message.error(error)
      })
    },
    handleCurrentChange(val){ // 跳转到某一页
      let param = this.queryForm
      this.pageNo = val
      param.page = this.pageNo
      this.list(param)
    },
    search() {
      let param = this.queryForm
      this.pageNo = 1
      param.page = this.pageNo
      this.list(param)
    },
    del(row) {
      this.$confirm('确定要删除吗？', '提示').then(()=>{
        del(row.id).then(data=>{
          console.log(data)
          let param = this.queryForm
          param.page = this.pageNo
          this.list(param)
        }).catch(error=>{
          this.$message.error(error)
        })
      }).catch(error=>{
        this.$message.error(error)
      })
    },
    append() {
      this.addVisible=true
    },
    hideDialog() {
      // 关闭窗口
      this.addVisible = false
      this.editVisible = false
    },
    edit(row) {
      this.editVisible = true
      this.formData = row
    }
  }
}
</script>

<style scoped>

</style>