<template>
  <div>
    <div>
      <el-form ref="queryForm" :inline="true" :model="queryForm" label-width="80px">
        <el-form-item>
          <el-input v-model="queryForm.content" placeholder="按内容查询"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" icon="el-icon-search" @click="search">查询</el-button>
          <el-button type="success" icon="el-icon-plus" @click="append">新增</el-button>
        </el-form-item>
      </el-form>
    </div>
    <el-table
        :data="tableData"
        stripe
        style="width: 100%">
      <el-table-column
          prop="id"
          label="ID"
          width="180">
      </el-table-column>
      <el-table-column
          prop="author"
          label="评论者"
          width="180">
      </el-table-column>
      <el-table-column
          prop="email"
          label="邮箱"
          width="180">
      </el-table-column>
      <el-table-column
          prop="ip"
          label="IP地址"
          width="180">
      </el-table-column>
      <el-table-column
          prop="createDate"
          label="评论时间"
          width="180">
      </el-table-column>
      <el-table-column
          prop="content"
          label="内容"
          width="180">
      </el-table-column>
      <el-table-column
          prop="status"
          label="状态"
          width="180">
      </el-table-column>
      <el-table-column
          align="center"
          width="150px"
          label="操作">
        <template slot-scope="scope">
          <el-link @click="del(scope.row)" :underline="false" type="danger" class="el-icon-delete">删除</el-link>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
        background
        layout="total, prev, pager, next, jumper"
        @current-change="handleCurrentChange"
        :current-page="pageNo"
        :total="total">
    </el-pagination>
  </div>
</template>

<script>
import {del, query} from "@/api/comment";

export default {
  name: "Index",
  components: '',
  data() {
    return {
      queryForm: {
        content: '',
      },
      tableData: [],
      total: 0,
      pageNo: 1,
    }
  },
  mounted() { // 挂载，显示时自动执行该方法？
    this.list({"page": this.pageNo})
  },
  methods: {
    list(param) {
      query(param).then(data => {
        this.tableData = data.list
        this.total = data.total
      }).catch(error => {
        this.$message.error(error)
      })
    },
    handleCurrentChange(val) { // 跳转到某一页
      let param = this.queryForm
      this.pageNo = val
      param.page = this.pageNo
      this.list(param)
    },
    search() {
      let param = this.queryForm
      this.pageNo = 1
      param.page = this.pageNo
      this.list(param)
    },
    del(row) {
      this.$confirm('确定要删除吗？', '提示').then(() => {
        del(row.id).then(data => {
          console.log(data)
          let param = this.queryForm
          param.page = this.pageNo
          this.list(param)
        }).catch(error => {
          this.$message.error(error)
        })
      }).catch(error => {
        this.$message.error(error)
      })
    },
    hideDialog() {
      // 关闭窗口
      this.editVisible = false
    },

  }
}
</script>

