<template>
  <el-aside width="200px">
    <el-menu
        router
        default-active="2"
        :unique-opened="true"
        class="el-menu-vertical-demo"
        @open="handleOpen"
        @close="handleClose">
      <template v-for="(menu, index) in menus" >
        <el-submenu :index="menu.children[0].route" :key="index">
          <template slot="title">
            <i :class="menu.icon"></i>
            <span>{{menu.name}}</span>
          </template>
          <template v-for="(child, i) in menu.children">
            <el-menu-item :index="child.route" :key="i">
              <i :class="child.icon"></i>
              {{ child.name }}
            </el-menu-item>
          </template>
        </el-submenu>
      </template>
    </el-menu>
  </el-aside>
</template>

<script>
import menus from '../../api/menu'
export default {
  name: "Left",
  data() {
    return {
      menus: menus,
    }
  },
  methods:{
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    }
  }
}
</script>

<style scoped>
.el-aside{
  background-color: white;
  position: absolute;
  top: 60px;
  left: 0px;
  bottom: 0px;
  border-right:1px solid #e6e6e6;
  box-shadow: 10px 0px 10px -6px #e0e0e0;
}
</style>