<template>
  <div class="whitebg notice">
    <h2 class="htitle">{{channel.name}}</h2>
    <ul>
      <li v-for="(item,index) in articles.slice(0,4)" :key="index">
        <a href="/">{{ item.title }}</a>
      </li>
    </ul>
  </div>
</template>

<script>
import articles from "@/data/articles";
import {queryByPos} from "@/api/front";
import {getTop} from "@/api/front";

export default {
  name: "FNotice",
  data() {
    return {
      articles: articles,
      channels: [],
      channel: {},
    }
  },
  mounted() {
    queryByPos('f').then(data => {
      this.channels = data.data
      this.channel = this.channels[0]
      getTop(this.channel.id, 4).then(data=>{
        this.articles = data.list
      })
    }).catch(error => {
      this.$message.error(error)
    })
  },
}
</script>

