<template>
  <div class="login-page">
    <div class="login-page-bg">
      <h3>CMS后台登录系统</h3>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item prop="userName">
          <el-input placeholder="请输入用户名" v-model="form.userName"></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input placeholder="请输入密码" type="password" v-model="form.password"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" :loading="loading" @click="submit">立即登录</el-button>
          <el-button>取消</el-button>
        </el-form-item>
        <el-form-item>
          版权所有@23233CopyRight
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return{
      form:{
          userName:'admin',
          password:'1233',
      },
      rules:{
        userName:[{required:true, message:'请录入用户名'}],
        password:[{required:true, message:'请录入密码'}],
      },
      loading: false
    }
  },
  methods:{
    submit(){
      this.$refs['form'].validate(valid => {
          if(valid) {
            this.loading = true
            //执行登录操作，dispatch调用actions中的方法，执行请求和回调操作
            this.$store.dispatch('login', this.form).then(()=>{
                // 执行跳转
                this.$router.push("/index")
            }).catch(error =>{
              // 弹出了错误提示框
              this.loading = false
              this.$message.error(error)
            })
          }
      })
    }
  }
}
</script>

<style scoped>
.login-page{
  height: 100%;
  background:url("../../assets/img/login-3.jpeg") no-repeat;
  background-size: cover;
}
.login-page-bg{
  position: absolute;
  top: 25%;
  left: 30%;
  width: 500px;
  bottom: 30%;
  background-color: #ffffff;
  /*background-color:transparent;*/
  border-radius: 8px;
  padding-top: 20px;
  padding-right: 50px;
  padding-bottom: 20px;
  text-align: center;
  color: #666666;
  -webkit-box-shadow: #666 0px 0px 10px;
  -moz-box-shadow: #666 0px 0px 10px;
  box-shadow: #666 0px 0px 10px;

}
.login-page-bg h2{
  margin-bottom: 40px;
}

</style>